<template>
	<view class="vocalConcertList">
		<view class="list column boxsiz">
			<template v-if="list.length > 0">
				<navigator class="box row bothSide boxsiz" hover-class="none" :url="'/pages/singer/vocalConcert?id=' + item.id" v-for="(item, index) in list" :key="index">
					<view class="pic">
						<template v-for="(v, index) in item.picture_and_vedio">
							<image :src="v.url" mode="aspectFill" lazy-load="true" fade-show="true"></image>
						</template>
					</view>
					<view class="con">
						<text class="t1">{{ item.title }}</text>
						<text class="t2">2020.02.29-12.31</text>
						<text class="t3">{{ item.title }}</text>
						<view class="text row bothSide verCenter">
							<view class="text-price">
								<text class="t1a">￥</text>
								<text class="t1b">{{ item.price }}</text>
							</view>
							<navigator class="btn">已预约</navigator>
						</view>
					</view>
				</navigator>
				<uni-load-more :status="more" v-if="list.length >= limit"></uni-load-more>
			</template>
			<template v-else>
				<view class="no-date column rowCenter">
					<view class="bg"></view>
					<text>暂无预约数据~</text>
				</view>
			</template>
		</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	data() {
		return {
			more: 'more',
			list: [],
			page: 1,
			flag: false,
			limit:10,
			type: ''
		};
	},
	onLoad(options) {
		this.type = options.type;
		this.getData();
	},
	onShow() {
		
	},
	onReachBottom() {
		if (this.flag) return;
		this.page++;
		this.getData(true);
	},
	methods: {
		getData(param) {
			if (param) {
				this.more = 'loading';
			}
			this.request(API.myReserve, 'GET', { limit: this.limit, page: this.page }).then(res => {
				if (res.code === 10000) {
					if (res.data.length > 0) {
						this.list = this.list.concat(res.data);
					} else {
						this.flag = true;
						this.more = 'noMore';
					}
				}
			});
		}
	},
	components: { uniLoadMore }
};
</script>

<style scoped lang="scss">
@import '../../assets/css/user/appointment.scss';
</style>
